 import simpleAddress from "@/components/simple-address/simple-address.nvue"
 import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
			title: "编辑地址",
			cityPickerValueDefault: [0, 0, 1],
			pickerText: '选择城市',
			editrMes:{
				name:"",
				phone:'',
				province:'',
				city:'',
				area:'',
				addres:'',// 详细地址
				isReading: true, // 防抖
			},
		}
	},
	onLoad(opt){
		console.log('opt',opt)
		this.editrMes = opt
		this.getStr(opt)
	},
	onShow() {
		this.isReading = true
	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		// 确认修改
		saveEditRess(){
			let _this = this
			let userinfo = this.$store.state.userInfo
			let data = this.editrMes
			data.token = userinfo.token
			console.log(this.editrMes)
			if(this.isReading){
				this.isReading = false
				uni.request({
					url: this.$http + '/api/user/editAddress',
					method: 'POST',
					data:{
						...data
					},
					success(res) {
						_this.isReading = true
						console.log('修改返回数据', res)
						if(res.data.code == 1){
							
							uni.navigateBack({
								
							})
							uni.showToast({
								title: '修改成功'
							})
						}
					}
				})
			}
			
		},
		openAddres() {
			this.$refs.simpleAddress.open();
		},
		// getStr(obj){
		// 	let str 
		// 	str = obj.province + '-' + obj.city + '-' + obj.area
		// 	this.pickerText = str
		// },
		onConfirm(e) {
			// console.log(e)
			this.pickerText = e.label // 地址label信息
			let box = e.label.split('-')
			console.log('box', box)
			this.editrMes.province = box[0]
			this.editrMes.city = box[1]
			this.editrMes.area = box[2]
		},
		// 地区选择器
		setCitys(val){
			console.log(val,'选择地区')
		},
	},
	components: {
		simpleAddress,
		Topbar
	}
}
